.dark-mode {
  background-color: #333;
  color: #fff;
}

.dark-mode header {
  background-color: #222;
}

.dark-mode .country {
  background-color: black !important;
  color: white !important;
}

/* Modal */
.dark-mode .modal-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: scroll;
}

.dark-mode .modal-content {
  background-color: black;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  max-width: 80%;
  width: 400px;
  /* height: 700px; */
  height: fit-content;
  border-radius: 10px;
  font-size: large;
}

.close {
